import React from "react";
import propTypes from "proptypes";
import './Movie.css'

function Movie(id, year, summary, title, poster,genres,trailer,key){
    
    return (<div className='movies'>
        <div className='movie'>
            <img src={poster} alt={title} title={title} />
        
        
        <div className='movie_data'><h3 className='movie_title'>{title}</h3>
        <h5 className='movie_year'>{year}</h5>
        <ul className='genres'>
            {genres.map(genre => <li className='genre'>{genre}</li>)}
        </ul>
        <p className='movie_summary'>{summary.slice(0,300)}...</p>
        
        </div>
           
        </div>
        

    </div>
    )
}


Movie.propTypes = {
    id: propTypes.number.isRequired,
    title : propTypes.string.isRequired,
    summary: propTypes.string.isRequired,
    poster : propTypes.string.isRequired,
    year: propTypes.number.isRequired,
    genres: propTypes.arrayOf(propTypes.string).isRequired
};

export default Movie;